<template>
    <div>
      <el-dialog
      width="700px"
      title="新增车辆信息"
      :visible.sync="isVisible"
      :close-on-click-modal="false"
      :close-on-press-escape="false"
      @close="cancel"
      >
        <el-form ref="busForm" :model="busForm" :rules="formRules" label-width="110px">
           <el-row>
               <el-col :span="12">
                  <el-form-item label="所属机构" prop="busOrgUuid">
                    <el-select v-model="busForm.busOrgUuid" @change="getLineList(busForm.busOrgUuid)">
                      <el-option
                        v-for="item in lineOrgs"
                        :key="item.orgUuid"
                        :label="item.orgName"
                        :value="item.orgUuid">
                      </el-option>
                    </el-select>
                  </el-form-item>
                </el-col>
                <el-col :span="12">
                 <el-form-item label="车牌号" prop="busPlateNumber">
                      <el-input v-model="busForm.busPlateNumber" placeholder="请输入车牌号" />
                  </el-form-item>
                </el-col>
            </el-row>
             <el-row>
              <el-col :span="12">
                <el-form-item label="所属线路" prop="busLineUuid">
                    <el-select v-model="busForm.busLineUuid">
                    <el-option
                        v-for="item in lines"
                        :key="item.lineUuid"
                        :label="item.lineName"
                        :value="item.lineUuid">
                    </el-option>
                    </el-select>
                </el-form-item>
            </el-col>
              <el-col :span="12">
                 <el-form-item label="车辆自编号" prop="busSelfCode">
                      <el-input v-model="busForm.busSelfCode" placeholder="请输入车辆自编号" />
                 </el-form-item>
               </el-col>
            </el-row>
            <el-row>
              <el-col :span="12">
                <el-form-item label="设备编号" prop="busDevUuid">
                    <el-select v-model="busForm.busDevUuid">
                    <el-option
                        v-for="item in devices"
                        :key="item.devUuid"
                        :label="item.devCode"
                        :value="item.devUuid">
                    </el-option>
                    </el-select>
                </el-form-item>
            </el-col>
            </el-row>
            <el-row>
                <el-col :span="6" :offset="18">
                    <el-button type="primary" @click="save">保存</el-button>
                    <el-button @click="cancel">取消</el-button>
                </el-col>
            </el-row>
       </el-form>
      </el-dialog>
    </div>
</template>
<script>
// api
import { getAllOrgList, linesList, deviceList } from '@/api/common'
import { createBus } from '@/api/management'

// 校验车牌号
import ruleVehicleNumber from '@/utils/extendValidate/extendValidate'
export default {
  name: 'BusCreate',
  data () {
    return {
      lineOrgs: [], // 机构
      lines: [], // 线路
      devices: [], // 设备编号
      devUuid: '',
      isVisible: false,
      busForm: {
        busOrgUuid: '',
        busLineUuid: '',
        busPlateNumber: '',
        busSelfCode: '',
        busDevUuid: ''
      },
      formRules: {
        busOrgUuid: [{required: true, message: '请选择所属机构', trigger: 'change'}],
        busLineUuid: [{required: true, message: '请选择线路', trigger: 'change'}],
        busPlateNumber: [{required: true, message: '请输入车牌号', trigger: 'blur'}, { validator: ruleVehicleNumber.isVehicleNumber, message: '车牌号格式有误', trigger: 'blur' }],
        busDevUuid: [{required: true, message: '请输入设备编号', trigger: 'blur'}]
      }
    }
  },
  props: {
    show: {
      type: Boolean,
      default: false
    }
  },
  watch: {
    show (isShow) {
      deviceList({devUuid: ''}).then(res => {
        this.devices = res
      })
      this.busForm.busLineUuid = ''
      this.lines = []
      this.isVisible = isShow
    }
  },
  mounted () {
    getAllOrgList().then(res => {
      this.lineOrgs = res
    })
    deviceList({devUuid: ''}).then(res => {
      this.devices = res
    })
  },
  methods: {
    save () {
      this.$refs.busForm.validate(isValid => {
        if (isValid) {
          const params = Object.assign({}, this.busForm)
          createBus(params).then(res => {
            this.$message({
              message: `新增路线成功!`,
              type: 'success'
            })
            this.cancel()
            this.$emit('updateList')
          })
        }
      })
    },
    cancel () {
      this.busForm.busLineUuid = ''
      this.lines = []
      this.$refs.busForm.resetFields()
      this.$emit('update:show', false)
      this.isVisible = false
    },
    // 通过组织机构的id和启用，是否删除来获取当前线路
    getLineList (lineOrgUuid) {
      this.busForm.busLineUuid = ''
      this.lines = []
      const params = {lineOrgUuid: lineOrgUuid, lineIsvalid: '1', lineDropFlag: 0}
      linesList(params).then(res => {
        this.lines = res
      })
    }
  }
}
</script>



